.darkMenu {
  display: block;
  max-width: 100vw;
  width: 100%;
  overflow: hidden;
  background-color: #0f0f0f;
}
.darkMenu .darkList {
  display: block;
  width: 100%;
  list-style: none;
}
.darkMenu .darkList li {
  display: inline-block;
  margin-right: 7%;
}
.darkMenu .darkList li:last-of-type {
  margin-right: 0;
}
.darkMenu .darkList li a {
  display: inline-block;
  font-size: 1em;
  color: #ffffff;
  line-height: 1.5em;
  padding: 0.2em 0 0.2em 20px;
