  background-image: -webkit-linear-gradient(bottom left, rgba(203, 169, 136, 0.7) 50%, #0f0f0f 50%);
  background-image: -o-linear-gradient(bottom left, rgba(203, 169, 136, 0.7) 50%, #0f0f0f 50%);
  background-image: linear-gradient(to top right, rgba(203, 169, 136, 0.7) 50%, #0f0f0f 50%);
